.button {
  margin-top: 2vh;
  border-radius: 20pt 20pt 20pt 20pt;
  border: 1pt solid #ffffff;
  width: 90%;
  height: 8vh;
  box-shadow: 0pt 4pt 8pt #00000034;
}

.button:hover {
  cursor: pointer;
}

.button p {
  padding-top: 1.5vh;
  font-size: 14pt;
  font-weight: 500;
}

.button.primary {
  background-color: #ec662b;
}

.button.secondary {
  background-color: #ea4d2c;
}

.button.selected {
  background-color: #9a9a9a;
}
