.payment-methods {
  width: 100%;
  margin-top: 2vh;
}
.payment-methods .title {
  display: flex;
  padding: 1vh 6vw;
  justify-content: space-between;
}
.payment-methods .title svg {
  fill: #1d1d1d;
}
.payment-methods .title h1 {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 13pt;
  font-weight: 600;
  color: #1d1d1d;
}
.payment-methods .item:nth-child(2) {
  border-top: 0.5pt solid #9a9a9a;
}

.payment-methods .item {
  display: flex;
  padding: 1vh 4vw;
  position: relative;
  height: min-content;
  border-bottom: 0.5pt solid #9a9a9a;
  background-color: #f4f4f4;
  justify-content: space-between;
}

.payment-methods .item .infos {
  margin-left: 2vw;
  margin-bottom: 0;
}

.payment-methods .item .infos h3 {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 9pt;
  color: #1d1d1d;
}

.payment-methods .item .infos h1 {
  padding: 0 !important;
  font-size: 13pt;
  font-weight: 600;
  color: #1d1d1d;
  margin: 0.5vh 0;
}
.payment-methods .item .radio {
  align-self: center;
}

.payment-methods .item .radio input[type="radio"] {
  border: 0px;
  width: 6vw;
  height: 4vh;
  vertical-align: middle;
  /* accent-color: green; */
}
