#failure {
  background-color: #00000034;
  background-image: url("../../images/backgrounds/bg-login.png");
  height: 100vh;
}

#failure .back {
  margin: 4vh 4vw;
  width: 10%;
  filter: drop-shadow(0pt 3pt 6pt #0000003e);
}

#failure .back:hover {
  cursor: pointer;
}
#failure .brand-failure {
  margin-top: 8vh;
}

#failure .brand-failure .content-area .header {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

#failure .brand-failure .content-area .header h3 {
  text-align: center;
  width: 60%;
}

#failure .brand-failure .content-area .payment-methods .item.selected {
  background-color: #ff8105;
}
#failure .brand-failure .content-area .payment-methods .item.selected .infos h1,
#failure
  .brand-failure
  .content-area
  .payment-methods
  .item.selected
  .infos
  h3 {
  color: white;
}
#failure .brand-failure .content-area .payment-methods h3 {
  margin-top: 2vh;
  text-align: center;
  text-decoration: underline;
  cursor: pointer;
}

#failure .desc-price {
  padding: 2vh 4vw;
  border-bottom: 0.5pt solid #9a9a9a;
  margin-top: 4vh;
  width: 100%;
}

#failure .desc-price div {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
#failure .desc-price div p {
  color: #000000;
  margin: 0;
  font-size: 13pt;
  font-weight: 600;
}

#failure .desc-price div p.number {
  font-family: Rubik;
}

#failure .total {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 2vh 4vw;
  margin-top: 2vh;
  margin-bottom: 1vh;
}
#failure .total p {
  color: #000000;
  margin: 0;
  font-size: 16pt;
  font-weight: 600;
}

#failure .total p.number {
  font-family: Rubik;
}

@media screen and (min-width: 600px) {
  #failure .back {
    width: 5%;
  }
}
