.post {
  padding: 6vh 15vw 5vh 5vw;
  position: relative;
}

.post h1 {
  color: white;
  font-size: 32pt;
}

.post h2 {
  color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  font-size: 16pt;
}

.post div,
.post div p {
  text-align: left;
  font-size: 12pt;
}

.post .ref {
  width: 100%;
  height: 5vh;
  margin-bottom: 5vh;
  position: relative;
  bottom: 0;
}
