#login {
  background-color: #00000034;
  background-image: url("../../images/backgrounds/bg-login.png");
  height: 100vh;
}

.brand-login {
  margin-top: 4vh;
}

.suggestion {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  margin-top: 6vh;
}

.suggestion h4 {
  width: 60%;
  text-align: end;
  margin-right: 6vw;
  color: white;
}
.suggestion .icon {
  margin-right: 8vw;
}

.content-area {
  position: fixed;
  bottom: 0;
  padding-bottom: 3vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  background-color: #ffffff;
  border-radius: 40pt 40pt 0 0;
  max-height: 85vh;
  overflow-y: scroll;
}

.content-area .header {
  margin-top: 3vh;
  margin-bottom: 6vh;
}

.header-login {
  margin-top: 3vh;
}
.header,
.header-login {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.header h3 {
  color: black;
  font-size: 20pt;
}

.arrowBack {
  fill: #ec662b !important;
  font-size: 24pt !important;
  position: absolute;
  left: 6vw;
}
.infos {
  margin-bottom: 2vh;
}
.infos p {
  color: black;
  margin: 0;
  font-weight: 550;
  font-size: 12pt;
  margin-bottom: 1vh;
}
