#finish-order {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0vh 2vw;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

#finish-order .content {
  width: 100%;
  background-color: white;
  border-radius: 30pt 30pt 30pt 30pt;
  box-shadow: 0pt 3pt 42pt #0000006a;
  padding: 1rem;
}

#finish-order .content .close {
  float: right;
  cursor: pointer;
}
#finish-order .content .close svg {
  fill: #ff8105;
  font-size: 24pt;
}

#finish-order .content h3 {
  text-align: center;
  margin-top: 5vh;
  font-weight: 500;
  font-size: 15pt;
  padding: 0vh 2vw;
}
#finish-order .content .search {
  border-radius: 12pt 12pt 12pt 12pt;
  box-shadow: 0pt 3pt 3pt #00000012;
}

#finish-order .content h4 {
  text-align: center;
  margin-top: 3vh;
  font-weight: 500;
  font-size: 14pt;
}

#finish-order .delivery-type .select {
  box-shadow: 0pt 3pt 6pt #00000029;
}
#finish-order .delivery-type .select p {
  font-size: 16pt;
  text-align: center;
  margin-bottom: 0;
}

#finish-order .delivery-type .disabled {
  border: 1pt solid #9a9a9a;
}

#finish-order .days {
  display: flex;
  width: 100%;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

#finish-order .days::-webkit-scrollbar {
  display: none;
}

#finish-order .days .day {
  border: 0.5pt solid #9a9a9a;
  border-radius: 13pt;
  /* width: fit-content; */
  width: 100%;
  padding: 1rem 1.6rem;
  cursor: pointer;
  margin-right: 1.5vw;
}

#finish-order .days .day p {
  color: #ff8105;
  text-align: center;
  margin-bottom: 0;
}

#finish-order .days .day h5 {
  font-family: Rubik;
  font-size: 16pt;
  text-align: center;
  color: #ff8105;
  margin-bottom: 0;
}

#finish-order .hours {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.1rem;
  margin-top: 2vh;
}

#finish-order .hours .hour {
  border: 0.5pt solid #9a9a9a;
  border-radius: 12pt;
  width: 100%;
  padding: 0.5rem;
  cursor: pointer;
  margin-top: 1vh;
}

#finish-order .hours .hour p {
  color: #ff8105;
  text-align: center;
  margin-bottom: 0;
}

#finish-order h5 {
  font-size: 10pt;
  text-align: center;
  margin-top: 2vh;
}

#finish-order .button {
  width: 100%;
}
