#products {
  padding: 5vh 4vw 0 4vw;
}
#products .header {
  display: flex;
  justify-content: space-between;
}

#products .header div {
  font-size: 1.2rem;
}

#products .header div:hover {
  cursor: pointer;
}

#products .header .user {
  color: #1d1d1d;
  text-decoration: underline;
}
#products .header .explore {
  color: #ec662b;
  font-weight: 500;
  margin: 0;
  padding: 0;
}

#products .welcome-msg {
  color: #1d1d1d;
  font-size: 1.4rem;
  font-weight: 500;
}
#products .delivery-type {
  display: flex;
  justify-content: space-between;
  margin-top: 2vh;
}
#products .delivery-type .select {
  width: 48%;
  box-shadow: 2pt 4pt 12pt #1d1d1d21;
  border-radius: 15pt 15pt 15pt 15pt;
  padding: 0.8rem;
}

#products .delivery-type .select:hover {
  cursor: pointer;
}

#products .delivery-type .select.selected {
  background-color: #ec662b;
}
#products .delivery-type .select .more {
  float: right;
}
#products .delivery-type .disabled svg {
  fill: #9a9a9a;
}
#products .delivery-type .disabled p {
  color: #9a9a9a;
}

#products .search {
  width: 100%;
  height: 6vh;
  margin-top: 4vh;
  border-radius: 16pt 16pt 16pt 16pt;
  box-sizing: border-box;
  border: 0.5px solid #9a9a9a;
  box-shadow: inset 0pt 3pt 6pt #00000014;
  color: #1d1d1d;
}
#products .search input {
  width: 90%;
  padding: 1em !important;
  margin: 0em !important;
  border: none;
  background-color: transparent;
  outline: none;
}

#products .search svg {
  fill: #9a9a9a;
  position: relative;
  float: right;
  margin-right: 2vw;
  margin-top: 1.5vh;
}

#products .categories::-webkit-scrollbar {
  display: none;
}

#products .categories {
  display: flex;
  width: 100%;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-top: 1vh;
}

#products .categories .category {
  border: 0;
  background-color: #ff8105;
  color: white;
  padding: 0 2vw;
  margin-right: 2vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#products .categories .category.selected {
  background-color: #ea4d2c;
}

#products .categories .category p {
  color: white;
  width: max-content;
}
#products .products {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  max-height: 100%;
  margin-top: 2vh;
  margin-bottom: 8vh;
}

#products .fixed {
  position: fixed;
  z-index: 1;
  bottom: 0;
  margin-bottom: 2vh;
  margin-left: 1vw;
}

#products .btn-space {
  height: 5vh;
}
/* pc */
@media screen and (min-width: 600px) {
  #products .products {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
