#welcome {
  height: 101vh;
  background-image: url("../../images/backgrounds/bg-login.png");
}

.explore {
  font-size: 15pt;
  text-align: end;
  margin-right: 6vw;
  padding-top: 6vh;
}

.explore:hover {
  cursor: pointer;
}

.info {
  padding-left: 5vw;
  font-weight: 500;
  margin-top: 5vh;
}

.info div p {
  margin: 0;
  margin-top: 0.3vh;
}
.info .weekdays {
  margin-bottom: 3vh;
}

.brand-welcome {
  margin-top: 13vh;
}

.message-area {
  width: 90%;
  margin-top: 6vh;
  margin-bottom: 2vh;
  background-image: linear-gradient(123deg, #ffffff65, #ffffff0d);
  backdrop-filter: blur(40pt);
}

.message-area h3 {
  font-size: 20pt;
  margin-top: 2vh;
  margin-bottom: 2vh;
  color: white;
}
.message-area .message p,
.brand-welcome .message p {
  font-size: 12pt;
  text-align: center;
  margin: 0;
  margin-bottom: 1vh;
}

.brand-welcome .iconMsg {
  display: flex;
}

.message-area .button {
  margin-bottom: 2vh;
}

.posts {
  position: relative;
}

.sticky-button {
  position: fixed;
  width: 90%;
  top: 85vh;
  z-index: 999;
  margin-left: 5vw;
}
.normal-button {
  position: fixed;
  width: 90%;
  top: 75vh;
  z-index: 999;
  margin-left: 5vw;
}

@media screen and (min-width: 600px) {
  .brand-welcome {
    margin-top: 10vh;
  }
}
