#success {
  background-color: #00000034;
  background-image: url("../../images/backgrounds/bg-login.png");
  height: 100vh;
}

#success .explore {
  width: 100%;
  padding-right: 4vw;
}

#success .brand-success {
  margin-top: 8vh;
}

#success .brand-success .content-area .header {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}

#success .brand-success .content-area .header h3 {
  text-align: center;
  width: 60%;
}
#success .brand-success .content-area .infos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 2vh;
}

#success .brand-success .content-area h1 {
  text-align: center;
  font-family: Rubik;
  font-size: 24pt;
}
