.box-itens {
  width: 100%;
  border-top: 1pt solid #9a9a9a;
  margin-top: 1vh;
  box-shadow: 0pt 4pt 8pt #00000033;
}

.box-itens .item {
  display: flex;
  padding: 1vh 4vw;
  position: relative;
  height: min-content;
  border-bottom: 0.5pt solid #00000026;
}

.box-itens .item img {
  max-width: 18%;
  max-height: 8vh;
  border-radius: 14pt;
  border: 1pt solid #707070;
}

.box-itens .item .infos {
  margin-left: 2vw;
  margin-bottom: 0;
}

.box-itens .item .infos h3 {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 10pt;
  color: #9a9a9a;
}

.box-itens .item .infos h1 {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 12pt;
  font-weight: 600;
}
.box-itens .item .infos h2 {
  margin: 0 !important;
  padding: 0 !important;
  font-size: 12pt;
  font-family: Rubik;
  color: #ff8105;
}

.box-itens .item .add-remove {
  display: flex;
  position: absolute;
  bottom: 0.5vh;
  right: 4vw;
}

.box-itens .item .add-remove .icon svg {
  fill: #ff8105;
}

.box-itens .item .add-remove h3 {
  margin: 0 !important;
  padding: 0 !important;
  font-family: Rubik;
  font-size: 12pt;
}

.box-itens .item .add-remove .remove svg {
  fill: #9a9a9a;
}

.box-itens .add-more {
  color: #1d1d1d;
  text-align: center;
  text-decoration: underline;
  padding: 1vh 0;
  font-weight: 600;
  cursor: pointer;
}
