#explore {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

#explore .close {
  margin: 4vh 4vw;
  position: absolute;
  top: 0;
  right: 0;
  width: 10%;
}

#explore .close:hover {
  cursor: pointer;
}

#explore .modal {
  background-color: #ec662b;
}

#explore .secondary {
  margin-bottom: 63vh;
  z-index: -1;
}

#explore .primary {
  margin-top: 1vh;
  box-shadow: 0pt 0pt 15pt #00000040;
  z-index: 1;
  height: 65vh;
  justify-content: center;
  align-items: center;
}

#explore .primary .option {
  display: flex;
  width: 70%;
  margin-top: 0.5vh;
  margin-bottom: 0;
  height: fit-content;
}

#explore .primary .option:hover {
  cursor: pointer;
}

#explore .primary .option:first-child {
  margin-top: 4vh;
}

#explore .primary .option h4 {
  color: black;
  font-size: 18pt;
  margin: none !important;
}
#explore .primary .option .icon {
  fill: #ec662b;
  margin-right: 4vw;
}
#explore .secondary .title {
  display: flex;
  margin-top: 2vh;
}
#explore .secondary .title h4 {
  color: white;
  font-size: 14pt;
}

/* iphone12 */
@media screen and (min-height: 800px) and (max-height: 896px) {
  #explore .primary .option:first-child {
    margin-top: 8vh;
  }
  #explore .primary .option {
    margin-top: 1vh;
  }
}

/* pc */

@media screen and (min-width: 600px) {
  #explore .close {
    width: 5%;
  }
}
