#cart .header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
  padding: 5vh 4vw 0 4vw;
}
#cart .header img {
  width: 13%;
  filter: drop-shadow(0pt 3pt 6pt #0000003e);
}
#cart .header img .share {
  filter: drop-shadow(1pt 6pt 10pt #0000003e);
}

#cart .welcome-msg {
  color: #1d1d1d;
  font-size: 1.4rem;
  font-weight: 500;
  padding: 0 4vw;
}

#cart h3 {
  margin-top: 2vh;
  padding: 0 4vw;
}

#cart .gift {
  display: flex;
  padding: 2vh 4vw;
  background-color: #ff8105;
  box-shadow: 0pt 4pt 8pt #00000033;
  align-items: center;
  cursor: pointer;
  margin-top: 2vh;
}

#cart .gift p,
#cart .new-box p {
  margin: 0;
  margin-left: 2vw;
  width: 90%;
  font-size: 1rem;
  font-weight: 500;
}

#cart .new-box {
  display: flex;
  padding: 2vh 4vw;
  background-color: #ea4d2c;
  box-shadow: 0pt 4pt 8pt #00000033;
  align-items: center;
  cursor: pointer;
  margin-top: 0.8vh;
  margin-bottom: 2vh;
}

#cart .desc-price {
  padding: 2vh 4vw;
  border-top: 1pt solid #9a9a9a;
  border-bottom: 1pt solid #9a9a9a;
}
#cart .desc-price div {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
#cart .desc-price div p {
  color: #000000;
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
}

#cart .desc-price div p.number {
  font-family: Rubik;
}

#cart .total {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 2vh 4vw 0vh 4vw;
}
#cart .total p {
  color: #000000;
  margin: 0;
  font-size: 1.2rem;
  font-weight: 600;
}

#cart .total p.number {
  font-family: Rubik;
}

#cart .button {
  margin-left: 5vw;
}

/* pc */

@media screen and (min-width: 600px) {
  #cart .header img {
    width: 4%;
  }
}
