#checkout .header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vh;
  padding: 5vh 4vw 0 4vw;
  width: 100%;
}

#checkout .header img {
  width: 13%;
  filter: drop-shadow(0pt 3pt 6pt #0000003e);
  margin-left: 1vw;
}

#checkout .header .user {
  color: #1d1d1d;
  text-decoration: underline;
}

#checkout .desc-price {
  padding: 2vh 4vw;
  border-bottom: 0.5pt solid #9a9a9a;
  margin-top: 2vh;
}
#checkout .desc-price div {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
#checkout .desc-price div p {
  color: #000000;
  margin: 0;
  font-size: 13pt;
  font-weight: 600;
}

#checkout .desc-price div p.number {
  font-family: Rubik;
}

#checkout .total {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 2vh 4vw;
  margin-top: 2vh;
}
#checkout .total p {
  color: #000000;
  margin: 0;
  font-size: 16pt;
  font-weight: 600;
}

#checkout .total p.number {
  font-family: Rubik;
}

#checkout .button {
  margin-left: 5vw;
}

/* pc */

@media screen and (min-width: 600px) {
  #checkout .header img {
    width: 4%;
    margin-left: 0vw;
  }
}
