#order {
  background-color: #00000034;
  background-image: url("../../images/backgrounds/bg-login.png");
  height: 100vh;
}

#order .brand-order {
  padding-top: 12vh;
}

.brand-order .content-area {
  position: fixed;
  bottom: 0;
  height: fit-content;
  padding-bottom: 3vh;
}
.brand-order .content-area .header {
  margin-bottom: 2vh;
}

.brand-order .content-area .infos p {
  text-align: justify;
  padding: 0 2rem;
}

.custom-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
  margin-top: 2vh;
  border-radius: 16pt 16pt 16pt 16pt;
  border: 1pt solid #9a9a9a;
  width: 90%;
  height: 8vh;
  background-color: #f4f4f4;
  color: #1d1d1d;
  text-align: center;
  font-size: 14pt;
  font-weight: 500;
  outline: none;
  background-image: linear-gradient(45deg, transparent 50%, #1d1d1d 50%),
    linear-gradient(135deg, #1d1d1d 50%, transparent 50%),
    linear-gradient(to right, transparent, transparent);
  background-position: calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat;
}

.custom-select:hover {
  cursor: pointer;
}
