.product {
  border-radius: 20pt 20pt 20pt 20pt;
  box-shadow: 0pt 5pt 10pt #0000001a;
  width: 100%;
  overflow: hidden;
  position: relative;
  height: fit-content;
}

.product img {
  max-height: 60%;
  max-width: 100%;
}
.product .favorite {
  cursor: pointer;
}

.product .favorite img {
  position: absolute;
  top: 0px;
  right: 0px;
  margin: 1rem;
  width: 15%;
}

.product .infos p {
  margin: 0;
}
.product .infos {
  padding: 0 2vw;
  margin-top: 1vh;
}
.product .infos .category {
  color: #9a9a9a;
  font-size: 0.6rem;
}
.product .infos .name {
  color: #ec662b;
  font-size: 1rem;
  width: max-content;
}

.product .infos div {
  display: flex;
  justify-content: space-between;
}
.product .infos div .price {
  font-family: Rubik;
  font-size: 1.2rem;
}
.product .infos div .add svg {
  fill: #ec662b;
  font-size: 1.8rem;
}

/* pc */
@media screen and (min-width: 600px) {
  .product .favorite img {
    width: 10%;
  }
}
