#product {
  height: 100vh;
}

#product img {
  z-index: -1;
  height: 60%;
  max-width: 100%;
}
#product .options {
  position: absolute;
  top: 0;
  left: 0;
  padding: 4em 1.5em;
}
#product .options div:hover {
  cursor: pointer;
}
#product .options img {
  width: 15%;
  margin-bottom: 2vh;
  filter: drop-shadow(1pt 6pt 10pt #0000003e);
}
#product .content-area {
  z-index: 1;
  box-shadow: 0pt -7pt 28pt #00000029;
  border-radius: 35pt 35pt 0 0;
}

#product .content-area .infos {
  margin-top: 5vh;
  padding: 0vh 6vw;
  width: 100%;
  margin-bottom: 0;
}
#product .content-area .infos h4 {
  color: #1d1d1d;
  font-weight: 600;
  margin: 0;
}
#product .content-area .infos .ingredients {
  margin-bottom: 1vh;
}
#product .content-area .infos h1 {
  color: #ff8105;
  font-size: 26pt;
  font-weight: 600;
}
#product .content-area .infos p {
  font-weight: 100;
  font-size: 10pt;
}

#product .content-area .price {
  display: flex;
  border-top: 0.5pt solid #9a9a9a;
  justify-content: space-between;
  margin-top: 2vh;
  margin-bottom: 0;
}
#product .content-area .infos .value {
  margin-top: 2vh;
}
#product .content-area .infos .value p:last-child {
  font-family: Rubik;
  font-size: 20pt;
}
#product .content-area .infos p {
  color: #1d1d1d;
}
#product .content-area .infos .add-remove {
  display: flex;
  width: 40%;
  justify-content: space-evenly;
  margin-top: 6vh;
}
#product .content-area .infos .add-remove .icon svg {
  fill: #ff8105;
}
#product .content-area .infos .add-remove h3 {
  font-family: Rubik;
  font-size: 18pt;
}

#product .content-area .infos .add-remove svg {
  font-size: 24pt;
}
#product .content-area .infos .add-remove .add svg {
  fill: #ea4d2c;
}

/* pc */

@media screen and (min-width: 600px) {
  #product .options {
    padding: 4em 6em;
  }
}
